@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600&display=swap);
body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}

body {
  padding: 20px 80px;
}

a {
  text-decoration: none;
  color: rgb(11, 11, 11);
}

.cart-icon{width:45px;height:45px;position:relative;display:flex;align-items:center;justify-content:center;cursor:pointer}.cart-icon .shopping-icon{width:24px;height:24px}.cart-icon .item-count{position:absolute;font-size:10px;font-weight:bold;bottom:12px}

.custom-button{min-width:165px;width:auto;height:50px;letter-spacing:0.5px;line-height:50px;padding:0 35px 0 35px;font-size:15px;background-color:black;color:white;text-transform:uppercase;font-weight:bolder;border:none;cursor:pointer;border:1px solid black;display:flex;justify-content:center;transition:all 0.2s ease-in}.custom-button:hover{background-color:white;color:black}.custom-button.google-login{background-color:#4285f4;border:1px solid #4285f4}.custom-button.google-login:hover{background-color:#2d68c7;color:#fff}.custom-button.inverted{background-color:white;color:black}.custom-button.inverted:hover{background-color:black;color:white}

.cart-item{width:100%;display:flex;height:80px;margin-bottom:15px}.cart-item img{width:30%}.cart-item .item-details{width:70%;display:flex;flex-direction:column;align-items:flex-start;justify-content:center;padding:10px 20px}.cart-item .item-details .name{font-size:16px}

.cart-dropdown{position:absolute;width:240px;height:340px;display:flex;flex-direction:column;padding:20px;border:1px solid black;background-color:white;top:90px;right:40px;z-index:5}.cart-dropdown .empty-message{font-size:18px;margin:50px auto}.cart-dropdown .cart-items{height:240px;display:flex;flex-direction:column;overflow:scroll}.cart-dropdown button{margin-top:auto}

.header{height:70px;width:100%;display:flex;justify-content:space-between;margin-bottom:25px}.header .logo-container{height:100%;width:70px;padding:20px}.header .options{width:50%;height:100%;display:flex;align-items:center;justify-content:flex-end}.header .options .option{padding:10px 15px;cursor:pointer}

.homepage{display:flex;flex-direction:column;align-items:center}

.directory-menu{width:100%;display:flex;flex-wrap:wrap;justify-content:space-between}

.menu-item{min-width:30%;height:240px;flex:1 1 auto;display:flex;align-items:center;justify-content:center;border:1px solid black;margin:0 7.5px 15px;overflow:hidden}.menu-item:hover{cursor:pointer}.menu-item:hover .background-image{transform:scale(1.1);transition:transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95)}.menu-item:hover .content{opacity:0.9}.menu-item.large{height:380px}.menu-item:first-child{margin-right:7.5px}.menu-item:last-child{margin-left:7.5px}.menu-item .background-image{width:100%;height:100%;background-size:cover;background-position:cover}.menu-item .content{height:90px;padding:0 25px;display:flex;flex-direction:column;align-items:center;justify-content:center;border:1px solid black;background-color:white;opacity:0.7;position:absolute}.menu-item .content .title{font-weight:bold;margin:0 6px 0;font-size:22px;color:#4a4a4a}.menu-item .content .subtitle{font-weight:lighter;font-size:16px}

.collection-item{width:22vw;display:flex;flex-direction:column;height:350px;align-items:center;position:relative}.collection-item .image{width:100%;height:95%;background-size:cover;background-position:center;margin-bottom:5px}.collection-item .custom-button{width:80%;opacity:0.7;position:absolute;top:255px;display:none}.collection-item:hover .image{opacity:0.8}.collection-item:hover .custom-button{opacity:0.85;display:flex}.collection-item .collection-footer{width:100%;height:5%;margin-top:8px;display:flex;justify-content:space-between;font-size:18px;font-weight:bold;border-top:1px solid black;padding:8px}.collection-item .collection-footer .name{width:80%;margin-bottom:15px}.collection-item .collection-footer .price{width:15%}

.collection-page{display:flex;flex-direction:column}.collection-page .title{font-size:38px;margin:0 auto 30px}.collection-page .items{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-gap:10px}.collection-page .items .collection-item{margin-bottom:30px}

.collection-preview{display:flex;flex-direction:column;margin-bottom:5rem}.collection-preview .title{font-size:28px;padding-bottom:1rem;margin-bottom:25px;border-bottom:2px solid black}.collection-preview .preview{display:flex;justify-content:space-between}

.collections-overview{display:flex;flex-direction:column}

.login-join{width:80%;display:flex;justify-content:space-between;margin:30px auto}

.sign-in{width:30vw;display:flex;flex-direction:column}.sign-in .title{margin:10px 0}.buttons{display:flex;justify-content:space-between}

.group{position:relative;margin:45px 0}.group .form-input{background:none;background-color:white;color:grey;font-size:18px;padding:10px 10px 10px 5px;display:block;width:100%;border:none;border-radius:0;border-bottom:1px solid grey;margin:25px 0}.group .form-input:focus{outline:none}.group .form-input:focus ~ .form-input-label{top:-14px;font-size:12px;color:#000}.group input[type='password']{letter-spacing:0.3em}.group .form-input-label{color:grey;font-size:16px;font-weight:normal;position:absolute;pointer-events:none;left:5px;top:10px;transition:300ms ease all}.group .form-input-label.shrink{top:-14px;font-size:12px;color:#000}

.sign-up{display:flex;flex-direction:column;width:30vw}.sign-up .title{margin:10px 0}

.checkout-item{width:100%;display:flex;min-height:100px;border-bottom:1px solid darkgrey;padding:15px 0;font-size:20px;align-items:center}.checkout-item .image-container{width:23%;padding-right:15px}.checkout-item .image-container img{width:100%;height:100%}.checkout-item .name,.checkout-item .quantity,.checkout-item .price{width:23%}.checkout-item .quantity{display:flex}.checkout-item .quantity .arrow{cursor:pointer}.checkout-item .quantity .value{margin:0 10px}.checkout-item .remove-button{padding-left:12px;cursor:pointer}

.checkout-page{width:55%;min-height:90vh;display:flex;flex-direction:column;align-items:center;margin:50px auto 0}.checkout-page .checkout-header{width:100%;padding:10px 0;display:flex;justify-content:space-between;border-bottom:1px solid darkgrey}.checkout-page .checkout-header .header-block{text-transform:capitalize;width:23%}.checkout-page .checkout-header .header-block:last-child{width:8%}.checkout-page .total{margin-top:30px;margin-left:auto;font-size:36px}

